#!/usr/bin/env node

/**
 * Background sync daemon
 *
 * Runs continuously in the background, syncing queued data every 60 seconds
 * Auto-started by SessionStart hook, runs independently of Claude sessions
 */

import { appendFile, mkdir } from "node:fs/promises";
import { dirname } from "node:path";
import { SYNC_INTERVAL_MS, SYNC_LOG_FILE } from "./config/constants.js";
import { loadSettings } from "./config/settings.js";
import { syncAllData } from "./supabase/sync.js";
import { writePidFile } from "./utils/daemon-manager.js";
import { getQueueStats } from "./utils/queue-manager.js";

// Custom logger that writes to sync.log (separate from plugin.log)
class SyncLogger {
  private async write(level: string, message: string, ...args: unknown[]): Promise<void> {
    try {
      await mkdir(dirname(SYNC_LOG_FILE), { recursive: true });
      const timestamp = new Date().toISOString();
      const argsStr = args.length > 0 ? ` ${JSON.stringify(args)}` : "";
      await appendFile(SYNC_LOG_FILE, `[${timestamp}] ${level}: ${message}${argsStr}\n`, "utf-8");
    } catch (error) {
      // Silently fail - don't crash daemon if we can't write logs
      console.error("Daemon failed to write log:", error);
    }
  }

  info(message: string, ...args: unknown[]): void {
    this.write("INFO", message, ...args);
  }

  error(message: string, error?: unknown): void {
    const errorStr = error instanceof Error ? error.stack : JSON.stringify(error);
    this.write("ERROR", `${message} ${errorStr}`);
  }

  debug(message: string, ...args: unknown[]): void {
    this.write("DEBUG", message, ...args);
  }
}

const syncLogger = new SyncLogger();

/**
 * Main daemon loop
 */
async function runDaemon(): Promise<void> {
  syncLogger.info("=== Sync daemon started ===");
  syncLogger.info(`PID: ${process.pid}`);
  syncLogger.info(`Sync interval: ${SYNC_INTERVAL_MS}ms (${SYNC_INTERVAL_MS / 1000}s)`);

  // Write PID file
  await writePidFile(process.pid);

  // Run sync loop
  while (true) {
    try {
      await syncCycle();
    } catch (error) {
      syncLogger.error("Sync cycle failed with exception", error);
    }

    // Wait for next cycle
    await sleep(SYNC_INTERVAL_MS);
  }
}

/**
 * Single sync cycle
 */
async function syncCycle(): Promise<void> {
  try {
    // Check if remote persistence is enabled
    const settings = await loadSettings();
    if (!settings.enableRemotePersistence) {
      syncLogger.debug("Remote persistence disabled, skipping sync (data still queuing locally)");
      return;
    }

    // Check if there's any data to sync
    const stats = await getQueueStats();
    const totalItems = stats.events + stats.sessions + stats.messages;

    if (totalItems === 0) {
      syncLogger.debug("No data to sync, skipping");
      return;
    }

    syncLogger.info(
      `Starting sync: ${stats.events} events, ${stats.sessions} sessions, ${stats.messages} messages`,
    );

    // Attempt sync
    const startTime = Date.now();
    const result = await syncAllData();
    const duration = Date.now() - startTime;

    if (result.success) {
      const { events, sessions, messages } = result.uploaded;
      const totalUploaded = events + sessions + messages;

      if (totalUploaded > 0) {
        syncLogger.info(
          ` Sync successful in ${duration}ms: ${events} events, ${sessions} sessions, ${messages} messages`,
        );
      } else {
        syncLogger.debug(`No data uploaded in ${duration}ms (may have been uploaded already)`);
      }
    } else {
      syncLogger.error(` Sync failed in ${duration}ms: ${result.error}`);

      // Don't spam logs on repeated failures
      if (result.error === "Not authenticated") {
        syncLogger.debug("Skipping sync - not authenticated");
      }
    }
  } catch (error) {
    syncLogger.error("Sync cycle error", error);
  }
}

/**
 * Sleep for specified milliseconds
 */
function sleep(ms: number): Promise<void> {
  return new Promise((resolve) => setTimeout(resolve, ms));
}

/**
 * Graceful shutdown handler
 */
function setupShutdownHandlers(): void {
  const shutdown = async () => {
    syncLogger.info("=== Daemon shutting down ===");
    process.exit(0);
  };

  process.on("SIGTERM", shutdown);
  process.on("SIGINT", shutdown);
}

// Start daemon
setupShutdownHandlers();
runDaemon().catch((error) => {
  syncLogger.error("Daemon crashed", error);
  process.exit(1);
});
