/**
 * Data synchronization utility
 *
 * Orchestrates upload of all queued data to Supabase
 */

import { logger } from "../utils/logger.js";
import { getQueueStats } from "../utils/queue-manager.js";
import { uploadChatDataWithRetry } from "./chat-uploader.js";
import { getSupabaseClient } from "./client.js";
import { uploadEventsWithRetry } from "./events-uploader.js";

export interface SyncResult {
  success: boolean;
  uploaded: {
    events: number;
    sessions: number;
    messages: number;
  };
  error?: string;
}

/**
 * Sync all queued data to Supabase
 * Returns stats about what was uploaded
 */
export async function syncAllData(): Promise<SyncResult> {
  try {
    // Check if there's any data to sync
    const stats = await getQueueStats();
    const hasData = stats.events > 0 || stats.sessions > 0 || stats.messages > 0;

    if (!hasData) {
      logger.debug("No data to sync");
      return {
        success: true,
        uploaded: { events: 0, sessions: 0, messages: 0 },
      };
    }

    logger.info(
      `Starting sync: ${stats.events} events, ${stats.sessions} sessions, ${stats.messages} messages`,
    );

    // Get authenticated Supabase client
    const supabase = await getSupabaseClient();
    if (!supabase) {
      logger.debug("Cannot sync: not authenticated or Supabase not configured");
      return {
        success: false,
        uploaded: { events: 0, sessions: 0, messages: 0 },
        error: "Not authenticated",
      };
    }

    // Upload events first (they can exist independently)
    const eventsResult = await uploadEventsWithRetry(supabase);
    if (!eventsResult.success) {
      return {
        success: false,
        uploaded: { events: eventsResult.uploaded, sessions: 0, messages: 0 },
        error: "Failed to upload events",
      };
    }

    // Upload chat data (sessions + messages)
    const chatResult = await uploadChatDataWithRetry(supabase);
    if (!chatResult.success) {
      return {
        success: false,
        uploaded: {
          events: eventsResult.uploaded,
          sessions: chatResult.uploaded.sessions,
          messages: chatResult.uploaded.messages,
        },
        error: "Failed to upload chat data",
      };
    }

    logger.info(" Sync completed successfully");
    return {
      success: true,
      uploaded: {
        events: eventsResult.uploaded,
        sessions: chatResult.uploaded.sessions,
        messages: chatResult.uploaded.messages,
      },
    };
  } catch (error) {
    logger.error("Sync failed with exception", error);
    return {
      success: false,
      uploaded: { events: 0, sessions: 0, messages: 0 },
      error: error instanceof Error ? error.message : "Unknown error",
    };
  }
}

/**
 * Sync data and return human-readable message
 */
export async function syncWithMessage(): Promise<string> {
  const result = await syncAllData();

  if (!result.success) {
    return `Sync failed: ${result.error || "Unknown error"}`;
  }

  const { events, sessions, messages } = result.uploaded;
  const total = events + sessions + messages;

  if (total === 0) {
    return "No data to sync";
  }

  const parts: string[] = [];
  if (events > 0) parts.push(`${events} events`);
  if (sessions > 0) parts.push(`${sessions} sessions`);
  if (messages > 0) parts.push(`${messages} messages`);

  return `Synced ${parts.join(", ")}`;
}
